.App {
  text-align: center;
  background-color: #cb9b8c;
}

div:not(#root):not(.App-header):not(.App):not(.App-content) {
  margin-left: 25px;
  text-align: left;
}

.App-header {
  position: fixed;
  top: 0;
  padding: 10px;
  background-color: #f3f3f3;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -webkit-box-shadow: 0px 5px 10px #d8d8d8;
          box-shadow: 0px 5px 10px #d8d8d8;
}


.App-title {
  text-align: left;
  margin-left: 35px;
  font-size: 3em;
  color: black;
  font-weight: 800;
}

.App-content {
  margin-top: 200px;
  -ms-flex-align: center;
      align-items: center;
}

button {
  margin: 15px;
  float: right;
}

.login, .generate-button {
  background-color: #f3f2f2; /* Green */
  border: none;
  color: black;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-weight: bold;
  font-size: 150%;
  -webkit-box-shadow: 0px 5px 10px #d6d6d6;
          box-shadow: 0px 5px 10px #d6d6d6;

}

.cat-title {
  font-size: 150%;
}

ul {
  list-style-type: none;
  color: black;
  margin: 0;
  padding: 0;
}

li {
  padding: 5px;
}

.container {
  font-weight: bold;
  font-size: 150%;
  padding: 25px;
  display: inline-block;
  color: black;
  background-color: #f3f2f2;
  margin-left: 50px;
  margin-bottom: 25px;
  -webkit-box-shadow: 0px 5px 10px lightgrey;
          box-shadow: 0px 5px 10px lightgrey;
}

.hidden {
  display: none;
}

#songs {
  float: left;
}

#artists {
  float: left;
}

#tempo {
  float: left;
}

#genre {
  float: left;
}






